<script setup>
import { ref, computed, watch } from "vue";
import {
  VoiceRecorder,
  VoiceUploader,
  STTClient,
  VoiceUploadManager,
  initializeDKVoiceWeb,
  getEnvironmentFromEnv,
  getApiUrl,
  initToken,
  clearToken,
  ThreeWayAnalyzer,
} from "@daekyo/dkvoiceweb";
import ChineseCompare from "./ChineseCompare.vue";

const activeTab = ref("voice-recorder");
const currentEnvironment = ref("dev");
const isRecording = ref(false);
const loading = ref(false);
const originalText = ref(""); //   
const result = ref({
  sttText: "",
  score: null,
  confidence: null,
  pronunciationScore: null,
  message: "",
  srResult: null,
});
const audioUrl = ref(null);
const tokenizerTestResult = ref(null);
const testText = ref(
  ""
);

//     
const authToken = ref("");
const isGettingToken = ref(false);
const tokenStatus = ref(null);

// authToken      
watch(authToken, (newToken) => {
  if (newToken && newToken.trim()) {
    initToken(newToken);
    console.log(
      "    -   :",
      newToken.substring(0, 20) + "..."
    );
  } else {
    clearToken();
    console.log("    -   ");
  }
});

// DKVoiceWeb  -  API URL   ( )
initializeDKVoiceWeb({
  subjectCode: "CTT",
  environment: getEnvironmentFromEnv(),
  enableLogging: getEnvironmentFromEnv() === "dev", //     
});

//     API   
const selectedFile = ref(null);
const audioDuration = ref(null); //   
const uploadSubjectCode = ref("CTT");

const uploadReferenceText = ref(
  ""
); //  
const isUploading = ref(false);
const uploadStep = ref(""); //    
const uploadResult = ref(null);
const presignedUrl = ref(""); // Presigned URL  

//    
const isUploadRecording = ref(false);
const uploadAudioUrl = ref(null);
const uploadSilenceThreshold = ref(0.03);
const uploadSilenceDuration = ref(2000);
let uploadRecorder = null; //  

//     
const authenticatedAudioUrl = ref(null);
const isLoadingAudio = ref(false);
const audioError = ref(null);

// silenceDuration, silenceThreshold     
const silenceThreshold = ref(0.03);
const silenceDuration = ref(2000);

//    
async function testJieba() {
  if (!testText.value.trim()) {
    alert("   .");
    return;
  }

  tokenizerTestResult.value = null;

  try {
    const { SegmentitTokenizer } = await import("./SegmentitTokenizer.js");
    const tokenizer = new SegmentitTokenizer();
    await tokenizer.initialize();

    const inputText = testText.value.trim();
    const tokens = await tokenizer.tokenize(inputText);
    const pinyinResult = await tokenizer.convertToPinyin(inputText);

    tokenizerTestResult.value = {
      tokens: tokens,
      pinyin: pinyinResult,
      tokenCount: tokens.length,
    };
  } catch (error) {
    console.error("  :", error);
    tokenizerTestResult.value = {
      tokens: [],
      pinyin: " ",
      tokenCount: 0,
      error: error.message,
    };
  }
}

//   
function clearTest() {
  testText.value = "";
  tokenizerTestResult.value = null;
}

// 50       computed property
const lowScoreWords = computed(() => {
  if (
    result.value?.analysis?.data?.srAnalysis?.wordPronunciation?.wordDetails
  ) {
    return result.value.analysis.data.srAnalysis.wordPronunciation.wordDetails.filter(
      (word) => word.accuracyScore < 50
    );
  }
  return [];
});

//    
const canUpload = computed(() => {
  return (
    selectedFile.value &&
    authToken.value &&
    uploadSubjectCode.value &&
    uploadReferenceText.value.trim() //     
  );
});

//   
async function getAuthToken() {
  isGettingToken.value = true;
  tokenStatus.value = null;

  try {
    //    API  
    const currentEnv = getEnvironmentFromEnv();
    let apiPath;
    if (currentEnv === "prod") {
      apiPath = "/api/dev/token";
    } else if (currentEnv === "stg") {
      apiPath = "/api/dev/token";
    } else {
      apiPath = "/api/dev/token";
    }

    const response = await fetch(apiPath, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        studentId: "1234567890",
        studentTypeCode: "DI", // DI:  , MA:    ( )
      }),
    });

    if (response.ok) {
      //  JSON 
      const responseData = await response.json();
      console.log("  (JSON):", responseData);

      // data   
      if (responseData.data) {
        authToken.value = responseData.data; // "Bearer " ,  

        //     
