let recorder = null; //  /
const uploader = new VoiceUploader(getApiUrl("presigned"));
const stt = new STTClient(getApiUrl("sttRequest"), getApiUrl("sttResult"));
const analyzer = new ThreeWayAnalyzer(); // 3-way  

//  VoiceUploadManager  -  API URL 
let voiceUploadManager = new VoiceUploadManager({
  presignApiUrl: getApiUrl("presigned"),
  azureEvaluateApiUrl: getApiUrl("azureEvaluate"),
});

function setupRecorder() {
  if (recorder) {
    recorder = null;
  }
  recorder = new VoiceRecorder({
    silenceThreshold: silenceThreshold.value,
    silenceDuration: silenceDuration.value,
  });
  recorder.on("start", () => {
    isRecording.value = true;
    result.value = null;
    audioUrl.value = null;
  });
  recorder.on("stop", async (blob) => {
    isRecording.value = false;
    audioUrl.value = URL.createObjectURL(blob);
    loading.value = true;
    try {
      const fileId = await uploader.upload(blob);
      const sttResult = await stt.requestSTT(fileId);

      // SR API    ()
      console.log("STT :", sttResult);

      // SR API    (DisplayText  NBest[0].Display )
      const sttText =
        sttResult.DisplayText ||
        (sttResult.NBest && sttResult.NBest[0]
          ? sttResult.NBest[0].Display
          : "") ||
        sttResult.text ||
        "";

      // 3-way   (    )
      const analysis = await analyzer.analyze(
        originalText.value,
        sttText,
        sttResult
      );

      // SR API    
      result.value = {
        message: "    ",
        sttText,
        srResult: sttResult, // SR API   
        confidence: sttResult.NBest?.[0]?.Confidence || 0,
        pronunciationScore:
          sttResult.NBest?.[0]?.PronunciationAssessment?.PronScore || 0,
        analysis: analysis, // 3-way   
      };
    } catch (e) {
      result.value = { message: " : " + e.message };
    }
    loading.value = false;
  });
  recorder.on("silence", () => {
    isRecording.value = false;
  });
}

function startRecording() {
  setupRecorder();
  recorder.start();
}
function stopRecording() {
  if (recorder) recorder.stop();
}

