//     
async function playAuthenticatedAudio(audioUrl) {
  if (!audioUrl || !authToken.value) {
    audioError.value = " URL    .";
    return;
  }

  isLoadingAudio.value = true;
  audioError.value = null;
  authenticatedAudioUrl.value = null;

  try {
    console.log("   :", audioUrl);
    console.log("  :", authToken.value.substring(0, 20) + "...");
    console.log("   :", authToken.value.length);

    console.log(
      "   (Bearer ):",
      authToken.value.substring(0, 30) + "..."
    );

    console.log("  URL:", audioUrl);

    // URL  
    const urlObj = new URL(audioUrl);
    console.log(" URL :");
    console.log("  - :", urlObj.hostname);
    console.log("  - :", urlObj.pathname);
    console.log("  - :", urlObj.search);

    console.log(
      "   (Bearer ):",
      authToken.value.substring(0, 30) + "..."
    );

    // CORS    URL 
    const proxyUrl = urlObj.pathname + urlObj.search; //    

    console.log("  URL:", audioUrl);
    console.log("  URL:", proxyUrl);

    //     
    const response = await fetch(proxyUrl, {
      method: "GET",
      headers: {
        Authorization: authToken.value, // Bearer  
        "Content-Type": "audio/wav",
      },
    });

    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }

    //  Blob 
    const audioBlob = await response.blob();
    console.log("    , :", audioBlob.size, "bytes");

    // Blob URL 
    const blobUrl = URL.createObjectURL(audioBlob);
    authenticatedAudioUrl.value = blobUrl;

    console.log("    ");

    //      
    setTimeout(() => {
      const audioElement = document.querySelector(".speech-audio-player");
      if (audioElement) {
        audioElement.play().catch((e) => {
          console.warn("   (  ):", e);
        });
      }
    }, 100);
  } catch (error) {
    console.error("    :", error);
    audioError.value = error.message;
  } finally {
    isLoadingAudio.value = false;
  }
}

//   
function stopAuthenticatedAudio() {
  if (authenticatedAudioUrl.value) {
    URL.revokeObjectURL(authenticatedAudioUrl.value);
    authenticatedAudioUrl.value = null;
  }
  audioError.value = null;
  console.log("   ");
}

