<script setup>
import { ref, watch } from "vue";
import { SimilarityChecker, TextComparator } from "@daekyo/dkvoiceweb";
import { SegmentitTokenizer } from "./SegmentitTokenizer.js";

// Props 
// eslint-disable-next-line no-undef
