const pronunciationThreshold = ref(90); //    (: 90)
const pronunciationErrors = ref([]); //   

// diff-match-patch  
const textComparator = new TextComparator();

//    ()
console.log(" TextComparator  :");
console.log("- diffsToHtml:", typeof textComparator.diffsToHtml);
console.log(
  "- diffsToHtmlWithMoveDetection:",
  typeof textComparator.diffsToHtmlWithMoveDetection
);
console.log("- detectMoves:", typeof textComparator.detectMoves);
console.log(
  "- Available methods:",
  Object.getOwnPropertyNames(Object.getPrototypeOf(textComparator)).filter(
    (name) => name !== "constructor"
  )
);
const diffResult = ref(null);
const diffHtml = ref("");
const enableMoveDetection = ref(true); //     (: true)

//     
const mispronunciations = ref([]);
const mispronunciationsHtml = ref("");
const showMispronunciations = ref(false);

const srResultJson = ref(""); // SR API  JSON   (  )
