const selectedCase = ref(""); //  
const autoFilledData = ref(false); //    

const checker = new SimilarityChecker();
const tokenizer = new SegmentitTokenizer();

// HTML   
function encodeHtml(text) {
  if (text === null || text === undefined) return "";
  return String(text)
    .replace(/&/g, "&amp;")
    .replace(/</g, "&lt;")
    .replace(/>/g, "&gt;")
    .replace(/"/g, "&quot;")
    .replace(/'/g, "&#39;");
}

// structuredDiffs HTML  
function structuredDiffsToHtml(structuredDiffs) {
  if (!Array.isArray(structuredDiffs)) return "";

  let html = "";
  structuredDiffs.forEach((item) => {
    const type = item?.type || "common";
    const value = encodeHtml(item?.value || "");

    switch (type) {
      case "added":
        html += `<span class="added">${value}</span>`;
        break;
      case "removed":
        html += `<span class="removed">${value}</span>`;
        break;
      case "moved":
        html += `<span class="moved">${value}</span>`;
        break;
      case "common":
      default:
        html += `<span>${value}</span>`;
        break;
    }
  });

  return html;
}

//       
watch(
  () => props.uploadResult,
  (newResult, oldResult) => {
    console.log(" watch !");
    console.log("  :", newResult);
    console.log("  :", oldResult);

    if (newResult) {
      console.log(" newResult .    ...");

      //     
      fillDataFromUploadResult(newResult);
    } else {
      console.log(" newResult null/undefined.");
    }
  },
  { deep: true, immediate: true }
);

//     
function fillDataFromUploadResult(uploadResult) {
  try {
    console.log(" fillDataFromUploadResult ");
    console.log("  uploadResult:", uploadResult);

    const evaluateData = uploadResult.evaluateResult?.data;
    console.log(" evaluateData:", evaluateData);

    if (!evaluateData) {
      console.log(" evaluateData .   .");

      //    
      if (uploadResult.evaluateResult) {
        console.log(" evaluateResult :", uploadResult.evaluateResult);
      }

      if (uploadResult.data) {
        console.log(" data :", uploadResult.data);
      }

      // speechText   
      let speechText = null;
      if (uploadResult.evaluateResult?.speechText) {
        speechText = uploadResult.evaluateResult.speechText;
        console.log(" evaluateResult.speechText :", speechText);
      } else if (uploadResult.speechText) {
        speechText = uploadResult.speechText;
        console.log(" uploadResult.speechText :", speechText);
      } else if (uploadResult.data?.speechText) {
        speechText = uploadResult.data.speechText;
        console.log(" uploadResult.data.speechText :", speechText);
      } else {
        console.log(" speechText   .");
      }

      //  speechText compareText 
      if (speechText) {
        compareText.value = speechText;
        console.log(" compareText :", compareText.value);
      }

      // nbest    
      let nbestData = null;
      if (uploadResult.evaluateResult?.nbest) {
        nbestData = uploadResult.evaluateResult.nbest;
        console.log(" evaluateResult.nbest ");
      } else if (uploadResult.evaluateResult?.NBest) {
        nbestData = uploadResult.evaluateResult.NBest;
        console.log(" evaluateResult.NBest ");
      } else if (uploadResult.nbest) {
        nbestData = uploadResult.nbest;
        console.log(" uploadResult.nbest ");
      } else if (uploadResult.NBest) {
        nbestData = uploadResult.NBest;
        console.log(" uploadResult.NBest ");
      } else if (uploadResult.data?.nbest) {
        nbestData = uploadResult.data.nbest;
        console.log(" uploadResult.data.nbest ");
      } else if (uploadResult.data?.NBest) {
        nbestData = uploadResult.data.NBest;
        console.log(" uploadResult.data.NBest ");
      }

      //  nbest  srResultJson 
      if (nbestData) {
        // nbest  Words    
        const nbestWithWords = nbestData.map((item) => ({
          ...item,
          // words ()   Words ()  
          Words: item.words || item.Words || [],
          Display: item.Display || compareText.value || " ",
          Confidence: item.Confidence || 0.9,
          Lexical: item.Lexical || compareText.value || " ",
          ITN: item.ITN || compareText.value || " ",
          MaskedITN: item.MaskedITN || compareText.value || " ",
        }));

        //  SR API    
        const fullSrResult = {
          Id: "auto-generated",
          RecognitionStatus: "Success",
          DisplayText: compareText.value || " ",
          NBest: nbestWithWords,
          //   
          SNR: 20.0,
          Duration: 0,
          Offset: 0,
          Channel: 0,
        };

        srResultJson.value = JSON.stringify(fullSrResult, null, 2);
        console.log(
          "  SR API   srResultJson :",
          nbestData.length,
          " "
        );
        console.log(
          "  srResultJson.value:",
          srResultJson.value.substring(0, 200) + "..."
        );
        console.log("   SR :", fullSrResult);
      } else {
        console.log(" nbest    .");
      }

