    //  () 
    if (props.referenceText) {
      originalText.value = props.referenceText;
      console.log("  :", originalText.value);
    }

    //    (speechText )
    if (evaluateData.speechText) {
      compareText.value = evaluateData.speechText;
      console.log(" compareText :", compareText.value);
    } else {
      console.log(" evaluateData.speechText .");
    }

    // SR API  JSON  - nbest   
    let nbestData = null;

    //   nbest  
    if (evaluateData.nbest) {
      nbestData = evaluateData.nbest;
      console.log(" evaluateData.nbest ");
    } else if (evaluateData.NBest) {
      nbestData = evaluateData.NBest;
      console.log(" evaluateData.NBest ");
    } else if (evaluateData.srResult?.nbest) {
      nbestData = evaluateData.srResult.nbest;
      console.log(" evaluateData.srResult.nbest ");
    } else if (evaluateData.srResult?.NBest) {
      nbestData = evaluateData.srResult.NBest;
      console.log(" evaluateData.srResult.NBest ");
    }

    if (nbestData) {
      // nbest  Words    
      const nbestWithWords = nbestData.map((item) => ({
        ...item,
        // words ()   Words ()  
        Words: item.words || item.Words || [],
        Display: item.Display || compareText.value || " ",
        Confidence: item.Confidence || 0.9,
        Lexical: item.Lexical || compareText.value || " ",
        ITN: item.ITN || compareText.value || " ",
        MaskedITN: item.MaskedITN || compareText.value || " ",
      }));

      //  SR API    
      const fullSrResult = {
        Id: "auto-generated",
        RecognitionStatus: "Success",
        DisplayText: compareText.value || " ",
        NBest: nbestWithWords,
        //   
        SNR: 20.0,
        Duration: 0,
        Offset: 0,
        Channel: 0,
      };

      srResultJson.value = JSON.stringify(fullSrResult, null, 2);
      console.log(
        "  SR API   srResult :",
        nbestData.length,
        " "
      );
      console.log("   SR :", fullSrResult);
    } else if (evaluateData.srResult) {
      // nbest   srResult 
      srResultJson.value = JSON.stringify(evaluateData.srResult, null, 2);
      console.log(" srResult   ");
    } else {
      console.log(" nbest, srResult    .");
    }

    autoFilledData.value = true;
    console.log("      ");

    //   
    setTimeout(() => analyzeText(), 500);
  } catch (error) {
    console.error("     :", error);
  }
}

//    
function clearAutoFilledData() {
  autoFilledData.value = false;
  //  
  originalText.value =
    "";
  compareText.value = "";
  srResultJson.value = "";
  analysis.value = null;
  selectedCase.value = "";
}

