async function analyzeText() {
  if (!originalText.value || !compareText.value) return;

  try {
    // SegmentitTokenizer 
    await tokenizer.initialize();

    //    SegmentitTokenizer 
    const originalTokens = await tokenizer.tokenize(originalText.value);
    const compareTokens = await tokenizer.tokenize(compareText.value);

    console.log("  :", originalTokens);
    console.log("   :", compareTokens);

    // SR API  JSON 
    let srResult = null;
    console.log(" srResultJson.value :", srResultJson.value);
    console.log(" srResultJson.value :", typeof srResultJson.value);
    console.log(" srResultJson.value :", srResultJson.value?.length);

    if (srResultJson.value && srResultJson.value.trim()) {
      try {
        srResult = JSON.parse(srResultJson.value);
        console.log(" SR API  JSON  :", srResult);
      } catch (error) {
        console.warn(" SR API  JSON  :", error);
        alert(
          "SR API  JSON   . JSON  ."
        );
        return;
      }
    } else {
      console.log(
        " SR API  JSON  .   ."
      );
    }

    // SR API      (  )
    analysis.value = await checker.analyzeChineseDifferencesWithSR(
      originalText.value,
      compareText.value,
      srResult,
      pronunciationThreshold.value
    );

    //   analysis 
    analysis.value.originalWords = originalTokens;
    analysis.value.compareWords = compareTokens;

    // srResult analysis   (    )
    if (srResult) {
      analysis.value.srResult = srResult;
      console.log(" srResult analysis  :", srResult);
    }

    // SR Words index      
    console.log("     :");
    console.log(" analysis.value.srResult:", analysis.value.srResult);
    console.log(
      " analysis.value.srResult?.NBest:",
      analysis.value.srResult?.NBest
    );
    console.log(
      " analysis.value.srResult?.NBest?.[0]:",
      analysis.value.srResult?.NBest?.[0]
    );
    console.log(
      " analysis.value.srResult?.NBest?.[0]?.Words:",
      analysis.value.srResult?.NBest?.[0]?.Words
    );
    console.log(
      " analysis.value.srResult?.NBest?.[0]?.words:",
      analysis.value.srResult?.NBest?.[0]?.words
    );

    // Words  words       
    const wordsData =
      analysis.value.srResult?.NBest?.[0]?.Words ||
      analysis.value.srResult?.NBest?.[0]?.words;

    console.log(" wordsData :");
    console.log(
      " analysis.value.srResult?.NBest?.[0]?.Words:",
      analysis.value.srResult?.NBest?.[0]?.Words
    );
    console.log(
      " analysis.value.srResult?.NBest?.[0]?.words:",
      analysis.value.srResult?.NBest?.[0]?.words
    );
    console.log("  wordsData:", wordsData);

    if (analysis.value.srResult && analysis.value.srResult.NBest && wordsData) {
      pronunciationErrors.value = extractPronunciationErrorChars(
        wordsData,
        pronunciationThreshold.value
      );

      // analysis     
      if (!analysis.value.pronunciationAnalysis) {
        analysis.value.pronunciationAnalysis = {};
      }
      analysis.value.pronunciationAnalysis.pronunciationErrors =
        pronunciationErrors.value;

      //     
      analysis.value.pronunciationAnalysis.threshold =
        pronunciationThreshold.value;
      analysis.value.pronunciationAnalysis.pronunciationStats = {
        mispronouncedWords: pronunciationErrors.value.length,
        averageAccuracy:
          pronunciationErrors.value.length > 0
            ? pronunciationErrors.value.reduce(
                (sum, error) => sum + error.accuracyScore,
                0
              ) / pronunciationErrors.value.length
            : 100,
        overallScore:
          pronunciationErrors.value.length > 0
            ? Math.max(0, 100 - pronunciationErrors.value.length * 5)
            : 100,
      };

      console.log(
        "SR Words     :",
        pronunciationErrors.value
      );
      console.log(
        "    :",
        analysis.value.pronunciationAnalysis.pronunciationErrors
      );
      console.log(
        "   :",
        analysis.value.pronunciationAnalysis.pronunciationStats
      );
    } else if (
      analysis.value.pronunciationAnalysis &&
      analysis.value.pronunciationAnalysis.hasData
    ) {
      // :      
      pronunciationErrors.value =
        analysis.value.pronunciationAnalysis.pronunciationErrors;

      // analysis     
      if (!analysis.value.pronunciationAnalysis) {
        analysis.value.pronunciationAnalysis = {};
      }
      analysis.value.pronunciationAnalysis.pronunciationErrors =
        pronunciationErrors.value;

      //      (   )
      if (!analysis.value.pronunciationAnalysis.pronunciationStats) {
        analysis.value.pronunciationAnalysis.threshold =
          pronunciationThreshold.value;
        analysis.value.pronunciationAnalysis.pronunciationStats = {
          mispronouncedWords: pronunciationErrors.value.length,
          averageAccuracy:
            pronunciationErrors.value.length > 0
              ? pronunciationErrors.value.reduce(
                  (sum, error) => sum + error.accuracyScore,
                  0
                ) / pronunciationErrors.value.length
              : 100,
          overallScore:
            pronunciationErrors.value.length > 0
              ? Math.max(0, 100 - pronunciationErrors.value.length * 5)
              : 100,
        };
      }

      console.log(
        "    :",
        pronunciationErrors.value
      );
      console.log(
        " :",
        analysis.value.pronunciationAnalysis.pronunciationStats
      );
    } else {
      pronunciationErrors.value = [];

      // analysis      
      if (!analysis.value.pronunciationAnalysis) {
        analysis.value.pronunciationAnalysis = {};
      }
      analysis.value.pronunciationAnalysis.pronunciationErrors = [];

      //      
      analysis.value.pronunciationAnalysis.threshold =
        pronunciationThreshold.value;
      analysis.value.pronunciationAnalysis.pronunciationStats = {
        mispronouncedWords: 0,
        averageAccuracy: 100,
        overallScore: 100,
      };

      console.log("   .");
    }

    // SR API    
    if (analysis.value.srResult) {
      console.log(" SR API  :", analysis.value.srResult);
      console.log(" DisplayText:", analysis.value.srResult.DisplayText);
      console.log(" NBest[0]:", analysis.value.srResult.NBest?.[0]);
      console.log(
        " PronunciationAssessment:",
        analysis.value.srResult.NBest?.[0]?.PronunciationAssessment
      );
      console.log(" Words:", analysis.value.srResult.NBest?.[0]?.Words);
      console.log(" ID:", analysis.value.srResult.Id);
      console.log(" SNR:", analysis.value.srResult.SNR);
      console.log(" Duration:", analysis.value.srResult.Duration);
      console.log(" Offset:", analysis.value.srResult.Offset);
      console.log(" Channel:", analysis.value.srResult.Channel);
    }

    console.log(" :", analysis.value);
  } catch (error) {
    console.error("   :", error);
    analysis.value = null;
  }
}

// diff-match-patch   
async function performDiffComparison() {
  if (!originalText.value || !compareText.value) {
    alert("    .");
    return;
  }

  try {
    console.log(" diff-match-patch  ");
    console.log(":", originalText.value);
    console.log(":", compareText.value);

    // TextComparator  
    console.log(" TextComparator :");
    console.log("- TextComparator:", textComparator);

    // TextComparator  

    // TextComparator   
    const result = textComparator.compareTexts(
      originalText.value,
      compareText.value
    );

    //  
    diffResult.value = result;

    // structuredDiffs  HTML 
    console.log(" structuredDiffs  HTML :");
    console.log("- result.structuredDiffs:", result.structuredDiffs);
    const htmlToDisplay = structuredDiffsToHtml(result.structuredDiffs);
    diffHtml.value = htmlToDisplay;

    //    
    if (analysis.value && analysis.value.srResult) {
      console.log("    ");
      const extractedMispronunciations =
        textComparator.extractMispronunciations(
          result.structuredDiffs,
          analysis.value.srResult
        );

      mispronunciations.value = extractedMispronunciations;
      console.log("   :", extractedMispronunciations);

      //   HTML 
      if (extractedMispronunciations.length > 0) {
        mispronunciationsHtml.value = textComparator.mispronunciationsToHtml(
          extractedMispronunciations,
          {
            showConfidence: true,
            showPosition: true,
            showDuration: true,
            cssClass: "mispronunciation-item",
            errorClass: "pronunciation-error",
            confidenceClass: "confidence-score",
          }
        );
        showMispronunciations.value = true;
        console.log("    -   ");
      } else {
        mispronunciationsHtml.value = "";
        showMispronunciations.value = false;
        console.log("    -   ");
      }
    } else {
      console.log(" SR      .");
      showMispronunciations.value = false;
    }

    console.log(" diff  :", result);
    console.log(" :", result.analysis.statistics);
    console.log("  :");
    console.log("-  :", result.originalText);
    console.log("-  :", result.comparedText);
    console.log("-  :", result.processedOriginal);
    console.log("-  :", result.processedCompared);
    console.log("-  :", result.analysis.changes.insertionText);
    console.log("-  :", result.analysis.changes.deletionText);
    console.log("- visualDiffs  :", !!result.visualDiffs);
    if (result.visualDiffs) {
      console.log("-  diffs:", result.visualDiffs);
      console.log(
        "-  HTML:",
        textComparator.diffsToHtml(result.visualDiffs)
      );
    } else {
      console.log("- visualDiffs !");
    }
    console.log("-  diffs:", result.diffs);
    console.log("-  HTML:", textComparator.diffsToHtml(result.diffs));
  } catch (error) {
    console.error(" diff    :", error);
    alert("    : " + error.message);
  }
}

// diff   
function clearDiffResults() {
  diffResult.value = null;
  diffHtml.value = "";
  mispronunciations.value = [];
  mispronunciationsHtml.value = "";
  showMispronunciations.value = false;
  console.log(" diff   ");
}

