//     
function analyzeMispronunciationStatistics() {
  console.log("     ");

  if (!mispronunciations.value || mispronunciations.value.length === 0) {
    console.log("     .");
    return;
  }

  const stats = {
    totalErrors: mispronunciations.value.length,
    averageConfidence: 0,
    confidenceDistribution: {
      high: 0, // 0.8-1.0
      medium: 0, // 0.6-0.79
      low: 0, // 0.4-0.59
      veryLow: 0, // 0.0-0.39
    },
    errorTypes: {},
    wordFrequency: {},
  };

  let totalConfidence = 0;

  mispronunciations.value.forEach((item) => {
    //   
    totalConfidence += item.confidence;

    if (item.confidence >= 0.8) stats.confidenceDistribution.high++;
    else if (item.confidence >= 0.6) stats.confidenceDistribution.medium++;
    else if (item.confidence >= 0.4) stats.confidenceDistribution.low++;
    else stats.confidenceDistribution.veryLow++;

    //   
    const errorType = item.errorType || "Unknown";
    stats.errorTypes[errorType] = (stats.errorTypes[errorType] || 0) + 1;

    //  
    const word = item.word || "Unknown";
    stats.wordFrequency[word] = (stats.wordFrequency[word] || 0) + 1;
  });

  stats.averageConfidence = totalConfidence / stats.totalErrors;

  console.log("   :", stats);

  //   HTML 
  const statsHtml = `
    <div class="mispronunciation-stats">
      <h3>    </h3>
      <div class="stats-container">
        <div class="stat-card">
          <h4> </h4>
          <p>  : <strong>${stats.totalErrors}</strong></p>
          <p> : <strong>${(stats.averageConfidence * 100).toFixed(
            1
          )}%</strong></p>
        </div>
        <div class="stat-card">
          <h4> </h4>
          <p> (80%+): <strong>${
            stats.confidenceDistribution.high
          }</strong></p>
          <p> (60-79%): <strong>${
            stats.confidenceDistribution.medium
          }</strong></p>
          <p> (40-59%): <strong>${
            stats.confidenceDistribution.low
          }</strong></p>
          <p>  (<40%): <strong>${
            stats.confidenceDistribution.veryLow
          }</strong></p>
        </div>
        <div class="stat-card">
          <h4>  </h4>
          ${Object.entries(stats.errorTypes)
            .map(([type, count]) => `<p>${type}: <strong>${count}</strong></p>`)
            .join("")}
        </div>
        <div class="stat-card">
          <h4>   </h4>
          ${Object.entries(stats.wordFrequency)
            .sort(([, a], [, b]) => b - a)
            .slice(0, 5)
            .map(
              ([word, count]) => `<p>${word}: <strong>${count}</strong></p>`
            )
            .join("")}
        </div>
      </div>
    </div>
  `;

  //  HTML  
  if (mispronunciationsHtml.value) {
    mispronunciationsHtml.value += statsHtml;
  }

  return stats;
}
function extractPronunciationErrorChars(srWords, threshold) {
  console.log(" extractPronunciationErrorChars ");
  console.log(" srWords:", srWords);
  console.log(" threshold:", threshold);
  console.log(" srWords :", typeof srWords);
  console.log(" srWords :", srWords?.length);
  console.log(" srWords ?", Array.isArray(srWords));

  const errorWords = [];

  // srWords    
  if (!Array.isArray(srWords)) {
    console.log(" srWords  :", srWords);
    return errorWords;
  }

  srWords.forEach((word, wordIndex) => {
    console.log(`  ${wordIndex}:`, word);
    console.log(`  ${wordIndex} :`, typeof word);
    console.log(`  ${wordIndex} :`, Object.keys(word || {}));
    console.log(`  ${wordIndex} word :`, word.word);
    console.log(`  ${wordIndex} Word :`, word.Word);

    // pronunciationAssessment ()  PronunciationAssessment () 
    const pronunciationAssessment =
      word.pronunciationAssessment || word.PronunciationAssessment;
    console.log(` pronunciationAssessment:`, pronunciationAssessment);

    if (pronunciationAssessment) {
      // accuracyScore ()  AccuracyScore () 
      const accuracyScore =
        pronunciationAssessment.accuracyScore ||
        pronunciationAssessment.AccuracyScore;
      console.log(` accuracyScore: ${accuracyScore}, : ${threshold}`);

      if (accuracyScore !== null && accuracyScore < threshold) {
        console.log(
          `   : ${word.word || word.Word} (${accuracyScore})`
        );
        errorWords.push({
          word: word.word || word.Word,
          wordIndex: wordIndex,
          accuracyScore: accuracyScore,
          errorType:
            pronunciationAssessment.errorType ||
            pronunciationAssessment.ErrorType ||
            "None",
        });
      } else if (accuracyScore === null) {
        console.log(
          ` accuracyScore null : ${word.word || word.Word} (${
            pronunciationAssessment.errorType ||
            pronunciationAssessment.ErrorType
          })`
        );
        // Omission      
        if (
          pronunciationAssessment.errorType ||
          pronunciationAssessment.ErrorType
        ) {
          errorWords.push({
            word: word.word || word.Word,
            wordIndex: wordIndex,
            accuracyScore: 0, // null  0 
            errorType:
              pronunciationAssessment.errorType ||
              pronunciationAssessment.ErrorType,
          });
        }
      } else {
        console.log(` : ${word.word || word.Word} (${accuracyScore})`);
      }
    } else {
      console.log(` pronunciationAssessment : ${word.word || word.Word}`);
    }
  });

  console.log("    :", errorWords);
  return errorWords;
}

//    
function updatePronunciationErrors() {
  console.log("   :", pronunciationThreshold.value);

  // SR Words     
  const wordsData =
    analysis.value?.srResult?.NBest?.[0]?.Words ||
    analysis.value?.srResult?.NBest?.[0]?.words;

  console.log(" updatePronunciationErrors wordsData :");
  console.log(
    " analysis.value?.srResult?.NBest?.[0]?.Words:",
    analysis.value?.srResult?.NBest?.[0]?.Words
  );
  console.log(
    " analysis.value?.srResult?.NBest?.[0]?.words:",
    analysis.value?.srResult?.NBest?.[0]?.words
  );
  console.log("  wordsData:", wordsData);

  if (wordsData) {
    pronunciationErrors.value = extractPronunciationErrorChars(
      wordsData,
      pronunciationThreshold.value
    );

    // analysis     
    if (analysis.value.pronunciationAnalysis) {
      analysis.value.pronunciationAnalysis.pronunciationErrors =
        pronunciationErrors.value;
      analysis.value.pronunciationAnalysis.threshold =
        pronunciationThreshold.value;

      //   
      analysis.value.pronunciationAnalysis.pronunciationStats = {
        mispronouncedWords: pronunciationErrors.value.length,
        averageAccuracy:
          pronunciationErrors.value.length > 0
            ? pronunciationErrors.value.reduce(
                (sum, error) => sum + error.accuracyScore,
                0
              ) / pronunciationErrors.value.length
            : 100,
        overallScore:
          pronunciationErrors.value.length > 0
            ? Math.max(0, 100 - pronunciationErrors.value.length * 5)
            : 100,
      };
    }

    console.log(
      "        :",
      pronunciationErrors.value
    );
    console.log(
      "   :",
      analysis.value.pronunciationAnalysis?.pronunciationStats
    );
  } else {
    console.log(" SR Words        .");
  }
}

function getWordClass(word, type) {
  if (!analysis.value) return "";

  if (type === "original") {
    // correctWords  (  )
    if (analysis.value.errors && analysis.value.errors.includes(word))
      return "error";
    if (analysis.value.omissions && analysis.value.omissions.includes(word))
      return "omission";
  } else if (type === "compare") {
    // correctWords  (  )
    if (analysis.value.insertions && analysis.value.insertions.includes(word))
      return "insertion";
  }

  return "";
}

//      (wordIndex )
function getWordPronunciationClass(word, wordIndex) {
  if (!analysis.value || !pronunciationErrors.value.length) return "";

  // SR Words  wordIndex   
  const isErrorWord = pronunciationErrors.value.some(
    (error) => error.wordIndex === wordIndex
  );

  return isErrorWord ? "pronunciation-error-word" : "";
}

//     (wordIndex )
function getWordPronunciationScore(word, wordIndex) {
  if (!analysis.value || !pronunciationErrors.value.length) return "N/A";

  const error = pronunciationErrors.value.find(
    (error) => error.wordIndex === wordIndex
  );

  return error ? error.accuracyScore.toFixed(1) : "N/A";
}

//  SR API  JSON  
async function loadSampleJson() {
  isLoadingJson.value = true;
  try {
    const response = await fetch("/SrResut.json");
    if (response.ok) {
      const srResult = await response.json();
      srResultJson.value = JSON.stringify(srResult, null, 2);
      console.log("SrResult.json   :", srResult);

      //    
      const wordCount = srResult.NBest?.[0]?.Words?.length || 0;
      const displayText =
        srResult.DisplayText || srResult.NBest?.[0]?.Display || "N/A";
      alert(
        `SrResult.json   !\n\n : ${displayText}\n : ${wordCount}`
      );
    } else {
      console.warn("SrResult.json    .");
      // :   JSON 
      loadDefaultSampleJson();
      alert(
        "SrResult.json        ."
      );
    }
  } catch (error) {
    console.warn("SrResult.json   :", error);
    // :   JSON 
    loadDefaultSampleJson();
    alert(
      "SrResult.json       ."
    );
  } finally {
    isLoadingJson.value = false;
  }
}

