  padding: 20px;
}

.success {
  color: #4ade80;
  font-weight: bold;
  font-size: 1.1em;
}

/*   */
@media (max-width: 768px) {
  .sr-api-grid {
    grid-template-columns: 1fr;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }
}

.no-data {
  color: #999;
