.diff-display :deep([style*="fff3cd"]) {
  background: linear-gradient(135deg, #ffaa44 0%, #ff8800 100%) !important;
  background-color: transparent !important;
  color: #000000 !important;
  border: 3px dashed #ff6600 !important;
  border-radius: 12px !important;
  box-shadow: 0 4px 12px rgba(255, 102, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.3) !important;
  font-weight: bold !important;
  position: relative !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3) !important;
  font-size: 16px !important;
  padding: 4px 8px !important;
  margin: 0 2px !important;
  transition: all 0.2s ease !important;
}

.diff-display :deep(span:hover) {
  transform: scale(1.05);
  z-index: 10;
}

/*   -    */
.diff-display :deep(.diff-html-added),
.diff-display :deep(.added),
.diff-display :deep(.insert),
.diff-display :deep(.inserted),
.diff-display :deep(.addition),
.diff-display :deep(.ins),
.diff-display :deep(ins) {
  background: linear-gradient(135deg, #00ff88 0%, #00cc44 100%) !important;
  color: #000000 !important;
  border: 3px solid #00cc44 !important;
  border-radius: 12px !important;
  box-shadow: 0 4px 12px rgba(0, 204, 68, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.3) !important;
  font-weight: bold !important;
  position: relative !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3) !important;
  font-size: 16px !important;
  padding: 4px 8px !important;
  margin: 0 2px !important;
  background-color: transparent !important;
  transition: all 0.2s ease !important;
}

.diff-display :deep(.diff-html-added:hover),
.diff-display :deep(.added:hover),
.diff-display :deep(.insert:hover),
.diff-display :deep(.inserted:hover),
